<template>
	<div id="CreateEmployee">
		<form v-on:submit.prevent="createdata">
	      <div class="row">
	        <div class="input-field col s12">
	          <textarea id="Employee" class="materialize-textarea" v-model="Employee"></textarea>
	          <label for="Employee">Employee Identification</label>
	        </div>
	      </div>	
	      <div class="row">
	        <div class="input-field col s7">
	          <textarea id="Fullname" class="materialize-textarea" v-model="Fullname"></textarea>
	          <label for="Fullname">Fullname</label>
	        </div>
	        <div class="input-field col s5">
	          <textarea id="Age" class="materialize-textarea" v-model="Age"></textarea>
	          <label for="Age">Age</label>
	        </div>
	      </div>
	      <div class="row">
	        <div class="input-field col s12">
	          <textarea id="Department" class="materialize-textarea" v-model="Department"></textarea>
	          <label for="Department">Department</label>
	        </div>
	      </div>
	      <div class="row">
	        <div class="input-field col s12">
	          <textarea id="Position" class="materialize-textarea" v-model="Position"></textarea>
	          <label for="Position">Position</label>
	        </div>
	      </div>
	      <router-link to="/" class="btn blue">Back</router-link>
	      <input type="submit" class="btn blue" value="Create">
		</form>
	</div>
</template>

<script>
import db from '@/components/firebaseInit'
	export default	{
		name: 'CreateEmployee',
		data () {
			return {
				Employee: null,
				Fullname: null,
				Age: null,
				Department: null,
				Position: null
			}
		},

		methods: {
			createdata() {
				db.collection('employees').add({
					employee_id: this.Employee,
					name: this.Fullname,
					age: this.Age,
					department: this.Department,
					position: this.Position
				}).then(docRef => {
					this.$router.push('/')
				})
			}
		}
	}
</script>


