<template>
	<div id="Dashboard">
		<ul class="collection with-header">
			<li class="collection-header">
				<h4>Create Employee
					<router-link to="/create" class="waves-effect waves-light btn secondary-content blue">Create</router-link>
				</h4>
			</li>
			<div v-for="item in employee">
				 <li class="collection-item">
				 	<div>{{item.employee_id}} | {{item.name}} 
				 		<router-link v-bind:to="{name: 'ViewEmployee', params: {'employee_id': item.employee_id}}" class="secondary-content">
				 			<i class="material-icons blue600">remove_red_eye</i>
				 		</router-link>
				 	</div>
				 </li>
			</div>
		</ul>
	</div>
</template>

<script>
import db from '@/components/firebaseInit'
	export default {
		name: 'Dashboard',
		data () {
			return {
				employee: []
			}
		},

		created ()	{
			db.collection('employees').orderBy('employee_id').get().then(querySnapshot => {
				querySnapshot.forEach(doc => {
					var data = {
						'doc_id' : doc.id,
						'employee_id' : doc.data().employee_id,
						'name' : doc.data().name,
						'age' : doc.data().age,
						'department' : doc.data().department,
						'position' : doc.data().position
					}
					this.employee.push(data)
				})
			})
		}
	}
</script>

<style>
.material-icons.blue600 {
	color: #03a9f4;
}
</style>


