<template>
	<div id="EditEmployee">
    	<form v-on:submit.prevent="editing">
    		 <div class="row">
		        <div class="input-field col s12">
		          <textarea disabled id="Employee" class="materialize-textarea" v-model="Employee"></textarea>
		          <label class="active" for="Employee">Employee Identification</label>
		        </div>
		      </div>	
		      <div class="row">
		        <div class="input-field col s7">
		          <textarea id="Fullname" class="materialize-textarea" v-model="Fullname"></textarea>
		          <label class="active" for="Fullname">Fullname</label>
		        </div>
		        <div class="input-field col s5">
		          <textarea id="Age" class="materialize-textarea" v-model="Age"></textarea>
		          <label class="active" for="Age">Age</label>
		        </div>
		      </div>
		      <div class="row">
		        <div class="input-field col s12">
		          <textarea id="Department" class="materialize-textarea" v-model="Department"></textarea>
		          <label class="active" for="Department">Department</label>
		        </div>
		      </div>
		      <div class="row">
		        <div class="input-field col s12">
		          <textarea id="Position" class="materialize-textarea" v-model="Position"></textarea>
		          <label class="active" for="Position">Position</label>
		        </div>
		      </div>
		     <router-link to="/" class="btn blue">Back</router-link>
		     <input type="submit" class="btn blue" value="Edit">
    	</form>
	</div>
</template>

<script>
import db from '@/components/firebaseInit'
	export default {
		name: 'EditEmployee',
		data () {
			return {
				Employee: null,
				Fullname: null,
				Age: null,
				Department: null,
				Position: null
			}
		},

		beforeRouteEnter (to, from, next)	{
			db.collection('employees').where('employee_id', '==', to.params.employee_id).get().then(querySnapshot => {
				querySnapshot.forEach(doc => {
					next(vm => {
						vm.Employee = doc.data().employee_id,
						vm.Fullname = doc.data().name,
						vm.Age = doc.data().age,
						vm.Department = doc.data().department,
						vm.Position = doc.data().position
					})
				})
			})
		},

		watch: {
			'$route' : 'fetchdata'
		},

		methods: {
			fetchdata () {
				db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(querySnapshot => {
					querySnapshot.forEach(doc => {
						this.Employee = doc.data().employee_id,
						this.Fullname = doc.data().name,
						this.Age = doc.data().age,
						this.Department = doc.data().department,
						this.Position = doc.data().position
					})
				})
			},

			editing () {
				db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(querySnapshot => {
					querySnapshot.forEach(doc => {
						doc.ref.update({
							employee_id: this.Employee,
							name: this.Fullname,
							age: this.Age,
							department: this.Department,
							position: this.Position
						}).then(() => this.$router.push({name: 'ViewEmployee', params: {'employee_id': this.Employee}}))
					})
				})
			}
		}
	}
</script>


