<template>
	<div id="ViewEmployee">
		<ul class="collection">
	      <li class="collection-item">{{doc_id}}</li>
	      <li class="collection-item">{{employee_id}}</li>
	      <li class="collection-item">{{name}}</li>
	      <li class="collection-item">{{age}}</li>
	       <li class="collection-item">{{department}}</li>
	        <li class="collection-item">{{position}}</li>
	    </ul>
	    <router-link to="/" class="waves-effect waves-light btn blue">Back</router-link>
	    <a @click="deleting" class="waves-effect waves-light btn red">Delete</a>
	    <router-link v-bind:to="{name: 'EditEmployee', params: {'employee_id': employee_id}}" class="waves-effect waves-light btn blue">Edit</router-link>
	</div>
</template>

<script>
import db from '@/components/firebaseInit'
	export default {
		name: 'ViewEmployee',
		data () {
			return {
				doc_id: null,
				employee_id: null,
				name: null,
				age: null,
				department: null,
				position: null
			}
		},

		beforeRouteEnter (to, from, next) {
			db.collection('employees').where('employee_id', '==', to.params.employee_id).get().then(querySnapshot => {
				querySnapshot.forEach(doc => {
					next(vm => {
						vm.doc_id = doc.id,
						vm.employee_id = doc.data().employee_id,
						vm.name = doc.data().name,
						vm.age = doc.data().age,
						vm.department = doc.data().department,
						vm.position = doc.data().position
					})
				})
			})
		},

		watch: {
			'$route' : 'fetchdata'
		},

		methods: {
			fetchdata() {
				db.collection('employees').where('employees', '==', this.$route.params.employee_id).get().then(querySnapshot => {
					querySnapshot.forEach(doc => {
						this.doc_id = doc.id,
						this.employee_id = doc.data().employee_id,
						this.name = doc.data().name,
						this.age = doc.data().age,
						this.department = doc.data().department,
						this.position = doc.data().position
					})
				})
			},

			deleting () {
				db.collection('employees').where('employee_id','==', this.$route.params.employee_id).get().then(querySnapshot => {
					querySnapshot.forEach(doc => {
						doc.ref.delete()
						this.$router.push('/')
					})
				})
			}
		}
	}
</script>


